<template>
  <div class="login_contaier">
    <div class="login_box">
      <h3>欢迎登陆测试系统<sup>+</sup></h3>
      <el-form label-width="0" ref="formRef" class="from_box" :model="loginForm" :rules="login_rules" >
        <el-form-item prop="username">
           <el-input type="text" prefix-icon="el-icon-s-custom" title="用户名" v-model="loginForm.username"></el-input>
        </el-form-item>
        <el-form-item prop="pwd">
          <el-input @keyup.enter="login" title="密码" type="password" prefix-icon="el-icon-magic-stick" v-model="loginForm.pwd" ></el-input>
        </el-form-item>
        <el-form-item >
        <el-button  type="primary" @click="login" >提交</el-button>
        <el-button type="info" @click="resetForm">重置</el-button>
        </el-form-item>
    </el-form>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('validatePass')
        }
      }
    }
    return {
      loginForm: {
        username: 'gly',
        pwd: '123'
      },
      rules: {
        username: [
          { trigger: 'blur', validator: validatePass }
        ]
      },
      login_rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 10, message: '用户名长度3-10个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetForm () {
      // console.log(this)
      this.$refs.formRef.resetFields()
    },
    login () {
      this.$refs.formRef.validate(async valid => {
      //  const { data: res } = await this.$http.post('login', this.loginForm)
        if (this.loginForm.pwd === '123' && this.loginForm.username === 'gly') {
          localStorage.setItem('dl', '1')
          this.$router.push('/jsManage')
        } else {
          this.$message.error('用户名密码错误')
        }
      })
    }
  }
}
</script>

