import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import './assets/css/global.css'
import './plugins/element.js'
import myButton from './components/my-button.vue'
import highlightPlugin from '@highlightjs/vue-plugin'
import 'highlight.js/styles/a11y-light.min.css' // 引入内置样式

Vue.use(highlightPlugin)

// 配置默认请求路径
axios.defaults.baseURL = 'https://api.apihubs.cn/'
Vue.config.productionTip = false
Vue.use(ElementUI)
Vue.prototype.$http = axios
Vue.component('my-component', {
  template: '<div>这是全局注册组件{{count}},定义属性：{{title}}<el-button @click="count++">点击++</el-button></div>',
  props: { title: String },
  data: function () {
    return {
      count: 0
    }
  }
})

Vue.component({ myButton })
// 定义菜单id,name名称
Vue.component('my-menu', { template: '<p :id="id" :title="id+title">{{title}}</p>', props: ['id', 'title'] })
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
