import VueRouter from 'vue-router'
import Login from '../views/login.vue'
import index from '../views/index.vue'
import Vue from 'vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/baidu',
    name: 'baidu',
    component: () => import('../views/baidu.vue')
  },
  {
    path: '/demo',
    name: 'demo',
    component: () => import('../views/demo.vue'),
    children: [
      { path: 'foo', component: { template: '<div>foo</div>' } },
      { path: 'bar', component: { template: '<div>bar</div>' } }]
  },
  {
    path: '/',
    name: 'Index',
    component: index,
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('../views/home.vue'),
        meta: {
          title: '首页',
          icon: 'el-icon-message'
        }
      }, {
        path: 'query',
        name: 'queyr',
        component: () => import('../views/query.vue'),
        meta: {
          title: '查询统计',
          icon: 'el-icon-message'
        },
        children: [
          {
            path: 'query',
            name: 'query',
            component: () => import('../views/query.vue'),
            meta: {
              title: '日期查询',
              icon: 'el-icon-message'
            }
          }
        ]
      }, {
        path: 'authManage',
        name: 'authManage',
        component: () => import('../views/testPage.vue'),
        meta: {
          title: '测试页面',
          icon: 'el-icon-message'
        }
      }, {
        path: 'jsManage',
        name: 'jsManage',
        component: () => import('../views/jsPage.vue'),
        meta: {
          title: '规则生成',
          icon: 'el-icon-message'
        }
      }
    ]
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  console.trace(from)
  const dl = localStorage.getItem('dl')
  if (dl === '1') {
    next()
  } else if (to.name === 'login') {
    next()
  } else {
    next('/')
  }
})
export default router
